import {usersAPI} from "../../api/axiosAPI";
import {AppDispatch} from "../store";
import {usersSlice} from "../reducers/usersReducer";

export const getArrayUsers = (currentPage: number, pageSize: number) => async (dispatch: AppDispatch) => {
    try {

        dispatch(usersSlice.actions.setCurrentPageNumber(currentPage))

        const response = await usersAPI.getArrayUsers(currentPage, pageSize)

        if (response) {
            dispatch(usersSlice.actions.setArrayUsers(response.items))
            dispatch(usersSlice.actions.setTotalUsersCount(response.totalCount))

        }
    } catch (e: any) {
        console.log('getArrayUsers error => ' + e.message)
    }
}


export const getCurrentPageNumber = (currentPage: number, pageSize: number) => async (dispatch: AppDispatch) => {
    try {
        dispatch(usersSlice.actions.setCurrentPageNumber(currentPage))

        const response = await usersAPI.getArrayUsers(currentPage, pageSize)

        if (response) {
            dispatch(usersSlice.actions.setArrayUsers(response.items))
        }
    } catch (e: any) {
        console.log('getCurrentPageNumber error => ' + e.message)
    }
}

export const doFollowOnUser = (userId: number) => async (dispatch: AppDispatch) => {
    try {
        const response = await usersAPI.doFollow(userId)

        if (response.data.resultCode === 0) {
            dispatch(usersSlice.actions.setFollow(userId))
        }
    } catch (e: any) {
        console.log('doFollowOnUser error => ' + e.message)
    }
}

export const doUnfollowOnUser = (userId: number) => async (dispatch: AppDispatch) => {
    try {
        const response = await usersAPI.doUnfollow(userId)

        if (response.data.resultCode === 0) {
            dispatch(usersSlice.actions.setUnfollow(userId))
        }
    } catch (e: any) {
        console.log('doUnfollowOnUser error => ' + e.message)
    }
}