import React from "react";
import {Pagination, Stack} from "@mui/material";


export const Paginator = (props: any) => {
    const setNewPage = (value: number) => {
        props.onPageChanged(value)
    }

    const getPagesCount = () => {
        const calcPagesCount = Math.ceil(props.totalUserCount / props.pageSize)
        let pagesCount = []
        for (let i = 1; i <= calcPagesCount; i++) {
            pagesCount.push(i)
        }
        return pagesCount;
    }

    const pagesCount = getPagesCount()

    return <Stack direction={'row'} justifyContent={'center'}>
            <Pagination hidePrevButton hideNextButton onChange={(e: any)=>setNewPage(e.target.textContent)} count={pagesCount.length}/>
        </Stack>


}






