import {createSelector} from "reselect";
import {RootState} from "../store";

export let simpleSelector = {
    users: (state: RootState) => {
        return state.usersSlice.users
    },

    currentPage: (state: RootState) => {
        return state.usersSlice.currentPage
    },

    totalUserCount: (state: RootState) => {
        return state.usersSlice.totalUserCount
    },

    pageSize: (state: RootState) => {
        return state.usersSlice.pageSize
    },

}


export let superSelector = {
    userWithPhotos: createSelector(simpleSelector.users, (users) => {
        return users.filter((u: any) => u.photos.large)
    })
}