import {Button, Stack, Typography} from "@mui/material";
import {NavLink} from "react-router-dom";
import userLogo from '../../../assets/img/user.png'
import style from './UsersMobile.module.scss'

export const UsersMobile = (props: any) => {
    return <Stack direction={'column'} justifyContent={'space-between'}>
        <Stack className={style.doCenter} flex={1} direction="column" justifyContent="center">
            {/*Good size 300x450*/}
            <NavLink to={'/Profile/' + props.id}>
                <img className={style.image} height={'auto'} src={props.photos.small ? props.photos.small : userLogo }/>
            </NavLink>
        </Stack>
        <Stack className={style.doCenter} flex={4}  direction="column" justifyContent="center">
            <NavLink className={style.linkDecorate} to={'/Profile/' + props.id}>
                <Typography variant={'h6'} textAlign={'center'}><span className={style.spanName}>{props.name}:</span>
                    <span className={style.spanStatus}> {props.status}</span> </Typography>
            </NavLink>
        </Stack>

        <Stack flex={3} direction="column" justifyContent="center">
            <Stack direction={'column'} justifyContent={'center'}>

                {
                    props.followed === false
                        ? <Button className={style.buttonFollow} onClick={() => {
                            props.follow(props.id)
                        }} variant="contained" color="error"></Button>

                        : <Button className={style.buttonFollow} onClick={() => {
                            props.unfollow(props.id)
                        }} variant="contained" color="success"> </Button>
                }
                <NavLink to={{pathname: '/PrivateMessage/PrivText/' + props.id}}
                         state={{senderPhotoById: props.photos.small ? props.photos.small : ''}}>
                    <Button className={style.buttonMessage} variant={'contained'}></Button>
                </NavLink>
            </Stack>
        </Stack>
    </Stack>
}