import React, {useEffect} from "react";
import {simpleSelector, superSelector} from "../../redux/selectors/usersSelector";
import {connect} from "react-redux";
import Users from "./Users";
import {doFollowOnUser, doUnfollowOnUser, getArrayUsers,} from "../../redux/action-creators/usersAction";
import {RootState} from "../../redux/store";

export type profilePropsType = ReturnType<typeof mapStateToProps> & typeof mapDispatchToPropsLite

const UsersContainer = (props: profilePropsType) => {
    //did mount
    useEffect(() => {
        props.getArrayUsers(props.currentPage, props.pageSize)
    }, [])

    const onPageChanged = (currentPage: number) => {
        props.getArrayUsers(currentPage, props.pageSize)
    }

    const follow = (id: number) => {
        props.doFollowOnUser(id)
    }
    const unfollow = (id: number) => {
        props.doUnfollowOnUser(id)
    }

    return <Users {...props} onPageChanged={onPageChanged} follow={follow} unfollow={unfollow}/>

}

const mapStateToProps = (state: RootState) => {

    return {
        usersAll: simpleSelector.users(state),
        usersWithPhoto: superSelector.userWithPhotos(state),
        currentPage: simpleSelector.currentPage(state),
        totalUserCount: simpleSelector.totalUserCount(state),
        pageSize: simpleSelector.pageSize(state),
    }

}

const mapDispatchToPropsLite = {
    getArrayUsers,
    doFollowOnUser,
    doUnfollowOnUser
}


const usersContainer = connect<any, any, any, any>(mapStateToProps, mapDispatchToPropsLite)(UsersContainer)

export default usersContainer