import {Button, Stack, Typography} from "@mui/material";
import {NavLink} from "react-router-dom";
import userLogo from '../../../assets/img/user.png'
import style from './UsersDesktop.module.scss'

export const UsersDesktop = (props: any) => {
    return (
        <Stack direction={'row'} justifyContent={'space-between'}>

            <Stack className={style.leftPanel} flex={1} direction="column" justifyContent="center">
                {/*Good size 300x450*/}
                <NavLink to={'/Profile/' + props.id}>
                    <img className={style.image} src={props.photos.small ? props.photos.small : userLogo }/>
                </NavLink>
            </Stack>

            <Stack className={style.middlePanel} flex={4} direction="column" justifyContent="center">
                <NavLink className={style.linkDecorate} to={'/Profile/' + props.id}>
                    <Typography variant={'h6'} textAlign={'left'}><span className={style.nickName}>{props.name}:</span> <span className={style.status}> {props.status}</span> </Typography>
                </NavLink>
            </Stack>

            <Stack className={style.rightPanel} flex={3} direction="column" justifyContent="center">
                <Stack direction={'row'} justifyContent={'center'}>

                    {
                        props.followed === false
                            ? <Button className={style.Button} onClick={() => {props.follow(props.id)}} variant="contained" color="error"></Button>
                            : <Button className={style.Button} onClick={() => {props.unfollow(props.id)}} variant="contained" color="success"> </Button>
                    }
                    {/*<NavLink to={'/PrivateMessage/PrivText/' + props.id}>*/}
                    <NavLink to={{pathname: '/PrivateMessage/PrivText/' + props.id}}
                             state={{senderPhotoById: props.photos.small ? props.photos.small : ''}}>
                        <Button variant={'contained'}></Button>
                    </NavLink>
                </Stack>
            </Stack>

        </Stack>
    )
}