import {Box, FormControlLabel, Stack, Switch} from "@mui/material";
import React, {useState} from "react";
import {Paginator} from "../common/Paginator/Paginator";
import {UsersMobile} from "./mobile/UsersMobile";
import {UsersTablet} from "./tablet/UsersTablet";
import {UsersDesktop} from "./desktop/UsersDesktop";
import {
    useIsDesktop,
    useIsMobile,
    useIsPortrait,
    useIsRetina,
    useIsTabletMax,
    useIsTabletMin
} from "../../hooks/responsive";
import {FailLoading} from "../common/FailLoading/FailLoading";


const UsersUI = (props: any) => {
    const [showOnlyWithPhoto, setShowOnlyWithPhoto] = useState(true)
    return (
        <Box flex={5} p={2}>
            <FormControlLabel control={<Switch defaultChecked onChange={e => showOnlyWithPhoto ? setShowOnlyWithPhoto(false) : setShowOnlyWithPhoto(true)}/>} label="  "/>
            <Stack direction={'row'} justifyContent={'space-between'}>
                <Box flex={10} p={2}>
                    {
                        showOnlyWithPhoto
                            ? props.usersWithPhoto.map((u: any, index:number) => <props.renderComponent key={index} {...props} {...u}/>)
                            : props.usersAll.map((u: any, index:number) => <props.renderComponent key={index} {...props} {...u}/>)
                    }
                    <Paginator {...props}/>
                </Box>
            </Stack>
        </Box>
    )

}


const Users = (props: any) => {
    const isDesktop = useIsDesktop()

    const isTabletMin = useIsTabletMin()
    const isTabletMax = useIsTabletMax()
    const isTablet = isTabletMin && isTabletMax

    const isMobile = useIsMobile()

    const isPortrait = useIsPortrait()
    const isRetina = useIsRetina()

    if (isDesktop) console.log('isDesktop')
    if (isTablet) console.log('isTablet')
    if (isMobile) console.log('isMobile')


    let renderComponent = undefined
    if (isDesktop) renderComponent = UsersDesktop
    if (isTablet) renderComponent = UsersTablet
    if (isMobile) renderComponent = UsersMobile

    if (isDesktop) return <UsersUI renderComponent={renderComponent} {...props}/>
    if (isTablet) return <UsersUI renderComponent={renderComponent} {...props}/>
    if (isMobile) return <UsersUI renderComponent={renderComponent} {...props}/>

    return <FailLoading/>


}

export default Users